.containerLost {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  z-index: 1;
  background-color: rgba(53, 49, 49, 0.795);
}
.hidden {
  display: none;
}
